@-webkit-keyframes shake {
  0%, 90%{
    -webkit-transform: translateX(-1px);
            transform: translateX(-1px);
  }
  20%, 80%{
    -webkit-transform: translateX(2px);
            transform: translateX(2px);
  }
  30%, 50%, 70%{
    -webkit-transform: translateX(-4px);
            transform: translateX(-4px);
  }
  40%, 60%{
    -webkit-transform: translateX(4px);
            transform: translateX(4px);
  }
}

@keyframes shake {
  0%, 90%{
    -webkit-transform: translateX(-1px);
            transform: translateX(-1px);
  }
  20%, 80%{
    -webkit-transform: translateX(2px);
            transform: translateX(2px);
  }
  30%, 50%, 70%{
    -webkit-transform: translateX(-4px);
            transform: translateX(-4px);
  }
  40%, 60%{
    -webkit-transform: translateX(4px);
            transform: translateX(4px);
  }
}

.fade {

}
.fade-enter {
  opacity: 0;
  -webkit-transform: scale(.5);
      -ms-transform: scale(.5);
          transform: scale(.5);
}
.fade-enter-active {
  opacity: 1;
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
}
.fade-leave {
  opacity: 1;
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
}
.fade-leave-active {
  opacity: 0;
  -webkit-transform: scale(.5);
      -ms-transform: scale(.5);
          transform: scale(.5);
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
}

.animationInOut-enter {
  -webkit-animation: animationIn .8s;
          animation: animationIn .8s;
}
.animationInOut-leave {
  -webkit-animation: animationOut .8s;
          animation: animationOut .8s;
}

/*Animacion de entrada*/
@-webkit-keyframes animationIn {
  0% {
    -webkit-transform: translateY(-3000px);
            transform: translateY(-3000px);
  }
  60% {
    -webkit-transform: translateY(25px);
            transform: translateY(25px);
  }
  75% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
  90% {
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
  }
}
@keyframes animationIn {
  0% {
    -webkit-transform: translateY(-3000px);
            transform: translateY(-3000px);
  }
  60% {
    -webkit-transform: translateY(25px);
            transform: translateY(25px);
  }
  75% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
  90% {
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
  }
}

/*Animacion de salida*/
@-webkit-keyframes animationOut {
  0% {
    -webkit-transform: translateY(5);
            transform: translateY(5);
  }
  60% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
  75% {
    -webkit-transform: translateY(25px);
            transform: translateY(25px);
  }
  100% {
    -webkit-transform: translateY(-3000px);
            transform: translateY(-3000px);
  }
}
@keyframes animationOut {
  0% {
    -webkit-transform: translateY(5);
            transform: translateY(5);
  }
  60% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
  75% {
    -webkit-transform: translateY(25px);
            transform: translateY(25px);
  }
  100% {
    -webkit-transform: translateY(-3000px);
            transform: translateY(-3000px);
  }
}

.like {
  cursor: pointer;
  width: 50px;
  height: 50px;
  background-image: url('../images/corazon.png');
  /*background-size: x y;*/
  background-size: 1450px 50px;
  bottom: -100px;
  position: fixed;
}

.like.is-liked {
  -webkit-animation-name: like, move;
          animation-name: like, move;
  -webkit-animation-duration: .8s, 5s;
          animation-duration: .8s, 5s;
  -webkit-animation-timing-function: steps(28), linear;
          animation-timing-function: steps(28), linear;
  /*2900 pixeles / 29 pasos o etapas = 100px*/
  /*background-position: -2800px 0;*/
  /*background-position: right;*/
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-iteration-count: infinite, 1;
          animation-iteration-count: infinite, 1;
}

@-webkit-keyframes like {
  0% {
    background-position: 0 0;
  }
  100% {
    /*background-position: -2800px 0;*/
    background-position: right;
  }
}

@keyframes like {
  0% {
    background-position: 0 0;
  }
  100% {
    /*background-position: -2800px 0;*/
    background-position: right;
  }
}

@-webkit-keyframes move {
  0%{
    position: absolute;
    bottom: 0;
  }
  100%{
    position: absolute;
    bottom: 110%;
  }
}

@keyframes move {
  0%{
    position: absolute;
    bottom: 0;
  }
  100%{
    position: absolute;
    bottom: 110%;
  }
}


