body {
  font-family: 'Allerta', sans-serif;
  margin: 0;
}


table {
  width: 100%;
}

th {
  background: #273b47;
  color: white;
}

th {
  padding: 10px 20px;
  text-align: left;
}

td {
  padding: 20px;
}

table, td, th {
  border: 1px solid #273b47;
  border-collapse: collapse;
}

input, textarea {
  outline: 0;
}

input:focus, textarea:focus {
  background: lightgray;
}

.contenedor {
  max-width: 800px;
  margin: auto;
  position: relative;
}

.background {
  background-color: #466173;
  background-image: url('../images/background.png');
  background-size: cover;
}

.portada.background.is-animated {
  background-image: url('../images/team-platzi-azul.jpg');
  background-repeat: no-repeat;
  -webkit-animation: background 15s linear infinite alternate;
          animation: background 15s linear infinite alternate;
}

@-webkit-keyframes background {
  0%{
    background-size: 200%;
    background-position: center;
  }
  20%{
    background-position: right top;
  }
  40%{
    background-size: 100%;
    background-position: center;
  }
  60%{
    background-position: left top;
  }
  80%{
    background-position: left -100px;
  }
  100%{
    background-size: 200%;
    background-position: right -100px;
  }
}

@keyframes background {
  0%{
    background-size: 200%;
    background-position: center;
  }
  20%{
    background-position: right top;
  }
  40%{
    background-size: 100%;
    background-position: center;
  }
  60%{
    background-position: left top;
  }
  80%{
    background-position: left -100px;
  }
  100%{
    background-size: 200%;
    background-position: right -100px;
  }
}

.button {
  border-radius: 12px;
  border: none;
  color: white;
  padding: 7px 12px;
  cursor: pointer;
  text-decoration: none;
  font-size: 18px;
  background: #fd324e;
  background: -webkit-gradient(linear, right top, left top, from(#fe344e), to(#ff7250));
  background: -webkit-linear-gradient(right, #fe344e, #ff7250);
  background: -o-linear-gradient(right, #fe344e, #ff7250);
  background: linear-gradient(to left, #fe344e, #ff7250);
  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#f3c5bd+0,e86c57+50,ea2803+51,ff6600+75,c72200+100;Red+Gloss */
