  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f3c5bd', endColorstr='#c72200',GradientType=0 ); */

}

.b-lazy {
  /*-webkit-transition: opacity 1s ease-in-out;*/
  /*-moz-transition: opacity 1s ease-in-out;*/
  /*-o-transition: opacity 1s ease-in-out;*/
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  max-width: 100%;
  opacity: 0;
}

.b-lazy.b-loaded {
 opacity: 1;
}

.titulo {
  font-family: 'Montserrat', sans-serif;
  font-size: 70px;
  margin-bottom: 0;
  margin-top: 70px;
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
}

.titulo span {
  text-decoration: underline;
}

.title-a {
  font-size: 24px;
  margin-top: 0;
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
}

.title-b {
  font-size: 50px;
  margin-bottom: 20px;
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;

}

.guitarras {
  color: #1F313C;
  margin-bottom: 60px;
}

.guitarras h2 {
  font-family: 'Montserrat', sans-serif;
  font-size: 35px;
}

.portada {

  color: white;
  padding: 20px;
  height: 500px;
}

.portada button {
  margin-bottom: 70px;
}


.menu {
  font-size: 24px;
  display: inline-block;
  position: absolute;
  right: 0;
}

.menu li {
  display: inline-block;
  margin-left: 30px;
}

.menu li:before{
  content: '∘';
  font-size: 25px;
  line-height: 20px;
  color: white;
}

.menu a {
  color: white;
  text-decoration: none;
}

.logotipo {
  display: inline-block;
  margin-left: 0;
}

.guitarra {
  margin: 10px 0px 40px;
  padding: 10px 2em;
  border: 1px solid #1F313C;
  border-radius: 3px;

  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  min-height: 250px;

  -webkit-transition: .3s;

  -o-transition: .3s;

  transition: .3s;
}
.guitarra:hover {
  -webkit-box-shadow: 10px 10px 0 #ff3149;
          box-shadow: 10px 10px 0 #ff3149;
}
.guitarra-name {
  font-size: 3em;
  margin: 0 0 .2em;
  padding-bottom: .2em;
  border-bottom: 2px dashed #1e313d;
}
.guitarra:nth-of-type(even) {
  -ms-flex-pack: end;
      justify-content: flex-end;
}
.guitarra:nth-of-type(even):hover {
  -webkit-box-shadow: -10px 10px 0 black;
          box-shadow: -10px 10px 0 black;
}
.guitarra:nth-of-type(even) .guitarra-image {
  left: 0;
  right: initial;
}
.guitarra-image {
  position: absolute;
  right: 0;
  bottom: 0;
}
.guitarra ol {
  padding: 0;
  list-style: none;
  counter-reset: features;
}
.guitarra li:before {
  counter-increment: features;
  content: counter(features) ". ";
}
