.header {
  position: relative;
}


.footer {
  background: #273b47;
  padding: 50px 10px;
}

.contacto {
  display: -ms-flexbox;
  display: flex;
  width: 300px;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-align: center;
      align-items: center;
}

.contacto img {
  display: block;
}

.contacto strong {
  display: block;
}

.contacto a {
  color: white;
  text-decoration: none;
  margin: 10px 10px 10px 0;
}


.footer .contenedor {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}


.formulario {
  display: -ms-flexbox;
  display: flex;
  width: 400px;
  color: white;
}


.col1, .col2 {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.col1 {
  margin-right: 15px;
}
.col2 {
  -ms-flex-align: start;
      align-items: flex-start;
}

.col2 .button {
  margin-top: 15px;
}

.intereses label {
  font-size: 12px;
}

.formulario label, .sexo, .intereses {
  margin-bottom: 10px;
}


.sexo {
  margin-top: 10px;
}


.tabla {
  margin: 50px 0;
}

.burguer-button {
  width: 40px;
  height: 40px;
  line-height: 40px;
  border-radius: 50%;
  background: #395667;
  display: none;
  text-align: center;
  cursor: pointer;
  position: fixed;
  left: 20px;
  top: 20px;
  color: white;
  z-index: 3;
}

.video-demo {
  max-width: 500px;
  width: 100%;
  border: 5px solid #3c5b6e;
}

.video-demo-contenedor {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin-bottom: 80px;
}

/*query para adaptar el sitio a tablets*/
@media screen and (max-width: 800px) {

  /*precios*/
  .tabla {
    margin: 50px 20px;
  }

  .tabla .contenedor {
    overflow: auto;
    border: 1px solid #273b47;
  }
  /*precios*/

  /*portada*/
  .titulo {
    font-size: 45px;
    margin-top: 30px;
  }

  .title-a {
    font-size: 18px;
  }

  .portada {
    height: auto;
  }

  .portada .button {
    display: inline-block;
    margin-bottom: 30px;
  }
  /*portada*/

  /*guitarras*/

  .video-demo-contenedor {
    margin-bottom: 30px;
  }

  .guitarras {
    text-align: center;
  }

  .izquierda, .derecha {
    float: none;
    position: static;
  }

  .title-b {
    margin: 0;
  }

  .guitarra li {
    list-style: none;
  }

  .guitarra.b {
    height: auto;
  }

  .contenedor-guitarra-b {
    position: static;
    margin: 0;
  }

  .contenedor-guitarra-a {
    padding: 0;
  }
  /*guitarras*/
}

/*cuando la pantalla sea mas pequena que la de un ipad (tablet mas usada)*/
@media screen and (max-width: 767px) {
  /*footer*/
  .footer .contenedor {
    display: block;
  }

  .contacto {
    margin: 0 auto;
    display: block;
  }

  .contacto a {
    display: block;
  }

  .formulario {
    display: block;
    margin: 0 auto;
    width: 300px;
  }

  .formulario label {
    font-size: 20px;
  }

  .sexo label {
    display: block;
  }

  .intereses label {
    display: block;
    font-size: 16px;
  }

  .col1 {
    margin: 0;
  }

  input[type="text"]{
    margin-bottom: 30px;
  }

  input[type="text"],
  input[type="email"] {
    font-size: 20px;
    padding: 5px;
  }

  input[type="radio"],
  input[type="checkbox"] {
    zoom: 1.5;
  }

  textarea {
    width: 100%;
  }
  /*footer*/
}

/*query para adaptar el sitio a telefonos*/
@media screen and (max-width: 650px) {

  .header {
    text-align: center;
  }

  .logotipo {
    margin: 0;
  }

  .menu {
    position: static;
    display: block;
  }

  /*afecta al primer li de esta lista*/
  .menu li:first-child {
    margin: 0;
  }

  .menu ul {
    padding: 0;
  }
}

@media screen and (max-width: 500px) {
  /*portada*/

  .portada .button {
    display: block;
  }

  .precios .header .logotipo {
    margin: 50px 0;
  }

  .portada .contenedor {
    position: static;
  }

  .header, .header .contenedor {
    position: static;
  }

  .burguer-button {
    display: inline-block;
  }

  .menu {
    background: rgba(255, 60, 88, .8);
    position: fixed;
    left: -100%;
    top: 0;
    z-index: 2;
    bottom: 0;
    right: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    width: 100%;
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
  }

  .menu ul {
    -ms-flex: 1 1;
        flex: 1 1;
  }

  .menu.active {
    left: 0;
  }

  .portada {
    text-align: center;
  }

  .titulo {
    font-size: 30px;
  }

  .title-a {
    font-size: 16px;
  }

  .menu li{
    display: block;
    margin: 0;
    padding: 8px 0;
    border-bottom: 1px solid white;
  }

  .menu li:before {
    display: none;
  }

  .menu li:first-child {
    border-top: 1px solid white;
  }
  /*portada*/

  /*guitarras*/
  .guitarra img{
    width: 200px;
    /*height: auto;*/
  }

  .title-b {
    font-size: 30px;
  }

  .video-demo-contenedor {
    margin: 0 10px 30px 10px;
  }
  /*guitarras*/

  /* formulario */
  .col2 {
    -ms-flex-align: stretch;
        align-items: stretch;
  }
  /* formulario */
}

.video-responsive-contenedor {
  position: relative;
  padding-top: 56.25%;
}

.video-responsive-src {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
}

